import React, { Component } from "react";
import { Col } from "react-bootstrap";
import EmailButton from "../ui/EmailButton";
import { Link } from "react-router-dom";
import { Button, Form } from "react-bootstrap";

class LessonCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      _id: props._id,
      loggedInUser: props.loggedInUser,
      title: props.title,
      description: props.description,
      location: props.location,
      participants: props.participants,
      user: props.creatorIdUser,
      teacher: props.creatorIdTeacher
    };
  }

  renderSpecificFields = () =>
    this.state.participants ? this.renderOfferFields() : null;

  renderOfferFields = () => (
    <Form.Group>
      <p>participants: {this.state.participants}</p>
    </Form.Group>
  );

  renderButtonCondition = () => {
    if (
      this.state.user &&
      this.state.user._id === this.state.loggedInUser._id
    ) {
      return this.renderEditButton();
    } else if (
      this.state.teacher &&
      this.state.teacher._id === this.state.loggedInUser._id
    ) {
      return this.renderEditButton();
    }
  };

  renderEditButton = () => (
    <Link to={`/delete/${this.state._id}`}>
      <Button variant="dark" type="button">
        Edit Lesson
      </Button>
    </Link>
  );

  SignUpHandler = () => {
    console.log(this.state.participants);
    // this.state.participants == 0 ? alert("sorry class is full") : null;
    this.state.participants--;
  };
  render() {
    const email = this.state.user
      ? this.state.user.email
      : this.state.teacher.email;

    return (
      <Col className="post-card" md={6}>
        <h3>{this.state.title}</h3>
        <p>Descripcion: {this.state.description}</p>
        <p>Localizacion: {this.state.location}</p>
        {this.renderSpecificFields()}
        <p>mail: {email}</p>
        <EmailButton value={this.state} />
        {this.renderButtonCondition()}
        <Button variant="warning" onClick={this.SignUpHandler}>
          Sign up
        </Button>
      </Col>
    );
  }
}

export default LessonCard;
