import axios from "axios";

export default class Services {
  constructor() {
    this._service = axios.create({
      baseURL: process.env.REACT_APP_URL,
      withCredentials: true
    });
  }

  getOne = id => this._service.get(`/lesson/${id}`);

  DeleteLesson = id => {
    return this._service.post("/lesson/delete", { id });
  };

  newLesson = (title, description, location, participants, user) => {
    return this._service.post("/lesson/createLesson", {
      title,
      description,
      location,
      participants,
      user
    });
  };

  editLesson = (id, title, description, location, participants) => {
    return this._service.post("/lesson/edit", {
      id,
      title,
      description,
      location,
      participants
    });
  };

  editEvent = (id, title, description, geoLocation) => {
    return this._service.post("/lesson/edit", {
      id,
      title,
      description,
      geoLocation
    });
  };

  newEvent = (title, description, geoLocation, user) => {
    return this._service.post("/lesson/createEvent", {
      title,
      description,
      geoLocation,
      user
    });
  };

  getAllLessons = () => this._service.get("/lesson/getAllLessons");
}
